.footer-div {
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    bottom: 0px;
    height: 6vh;
    width: 100vw;
    padding: .5vw 0px;
    background-color: var(--betteroptions-blue);
    z-index: 10;
}

#footer-name {
    color: white;
    padding: 1vh 0px;
}

.footer-image {
    height: 5vh;
    width: 10vw;
    border: 2px solid white;
    border-radius: 25px;
    cursor: pointer;
    opacity: .8;
}

.footer-image:hover {
    border: 2px solid var(--betteroptions-gold);
    opacity: 1;
}