#page-logo {
    position: fixed;
    padding: 2px 0px;
    right: 10px;
    margin-left: 10px;
    display: inline-block;
    height: 70px;
    z-index: 11;
}

#navbar-title {
    position: relative;
    text-align: left;
    font-size: 50px;
    font-weight: 900;
    top: 10px;
    padding-right: 10px;
    padding-bottom: 20px;
    display: inline-flex;
    justify-content: center;
    width: 90%;
    margin-left: 5%;
    line-height: 1;
    -webkit-text-stroke: 1px black;
    color: white;
    text-shadow:
        -6px 6px 0 #000,
      3px 3px 0 #000,
       1px -1px 0 #000,
       -1px 1px 0 #000,
        1px 1px 0 #000;
    -webkit-background-clip: text;
    background-clip: text;
    text-align:center;
    text-transform:uppercase;
    z-index: 11;
}

#navbar {
    position: relative;
    padding: 0px;
    margin-top: 5px;
    left: -10px;
    text-align: center;
    width: 100vw;
    background-color: var(--betteroptions-blue);
    z-index: 11;
}

