#navlinks {
    display: inline-flex;
    width: 100vw;
    justify-content: space-evenly;
}

.navbar-link-hide {
    display: none;
}

.navbar-link {
    color: var(--navlinks-color);
    font-size: 20px;
    text-decoration: none;
    font-weight: bold;
    display: inline-flex;
}

.navbar-link:hover {
    color: var(--betteroptions-gold);
}

.panel {
    width: 200px;
    height: 100px;
    background: green;
    margin-top: 10px;
}

.example-enter {
    height: 0px;
}

#navlinks-show {
    display: inline-flex;
    width: 100vw;
    justify-content: space-evenly;
    /* height: 100px; */
    animation: slideDown 1s ease;
}

#navlinks-hide {
    animation: slideUp 1s ease;
}

#fadeIn {
    animation: fadeIn 2s ease;
    display: inline-flex;
    color: var(--navlinks-color);
    width: 20vw;
    font-size: 20px;
    justify-content: space-evenly;
    padding: 2px 0px 2px 0px;
    cursor: pointer;
    position: relative;
    z-index: 11;
}

#fadeOut {
    display: none;
    animation: fadeOut 2s ease;
}

#fadeInNavLinks {
    animation: fadeIn 2s ease;
    display: inline-flex;
    width: 100vw;
    justify-content: space-evenly;
}

#fadeIn:hover {
    color: var(--betteroptions-gold);
}

#fadeIn:hover {
    color: var(--betteroptions-gold);
}

