#main-contact-div {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    width: 100vw;
    background:
        linear-gradient(
        rgba(234, 234, 234, 0.45),
        rgba(234, 234, 234, 0.45)
        );
    z-index: 2;
}

#background-contact {
    filter: grayscale(100%);
    position: fixed;
    width: 100%;
    height: calc(100vh - 96px);
    object-fit: cover;
    top: 96px;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
}

.mapouter {
    position: relative;
    text-align: right;
    height: 500px;
    width: 45vw;
    margin: 0px 2.5vw;
    z-index: 2;
}

.gmap_canvas {
    overflow: hidden;
    background: none!important;
    height: 500px;
    width: 45vw;
}

.iframe-map {
    height: 500px;
    width: 45vw;
}

.address-div {
    position: relative;
    text-align: center;
    height: 500px;
    width: 45vw;
    margin: 0px 2.5vw;
    background-color: var(--home-background);
    color: white;
    z-index: 2;
}

#addressInfo {
    display: block;
    vertical-align: middle;
    margin-top: 206px;
}

.address-title {
    font-size: 24px;
    font-weight: bold;
}

.address-line {
    font-size: 16px;
    font-weight: bold;
}