@keyframes appear {
    0% { opacity: 0 ;}
    50% { opacity: .5 ;}
    100% { opacity: 1 ;}
}

@-webkit-keyframes appear {
    0% { opacity: 0 ;}
    50% { opacity: .5 ;}
    100% { opacity: 1 ;}
}

@keyframes disappear {
    0% { opacity: 0 ;}
    50% { opacity: 1 ;}
    100% { opacity: 0 ;}
}

@-webkit-keyframes disappear {
    0% { opacity: 0 ;}
    50% { opacity: 1 ;}
    100% { opacity: 0 ;}
}

.entire-home-div {
    position: fixed;
    top: 127px;
    width: 100vw;
    background:
        linear-gradient(
        rgba(234, 234, 234, 0.45),
        rgba(234, 234, 234, 0.45)
        );
    z-index: 2;
}

#background-image {
    position: fixed;
    opacity: .4;
    width: 100%;
    height: calc(100vh - 96px);
    object-fit: cover;
    top: 96px;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
}

.main-home-div {
    position: relative;
    width: 100vw;
    padding-top: 3vh;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    z-index: 5;
}

.left-home-div {
    width: 46vw;
    background-color: var(--home-background);
}

.home-text {
    color: white;
    text-align: center;
    padding-top: 5px;
    font-size: 25px;
    width: 95%;
    margin-left: 2.5%;
    margin-bottom: 20px;
}

.home-text-smaller {
    color: white;
    text-align: center;
    padding-top: 5px;
    font-size: 20px;
    width: 95%;
    margin-left: 2.5%;
    margin-bottom: 10px;
}

.home-bullet {
    display: flex;
    flex-direction: row;
    width: 95%;
    margin-left: 2.5%;
    margin-bottom: 5px;
}

.bullet-image {
    height: 35px;
    border-radius: 10px;
}

.home-bullet-text {
    color: white;
    font-size: 20px;
    align-self: center;
    margin-left: 2%;
}

.photo-div {
    width: 46vw;
    background-color: var(--home-background);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.photo-home {
    width: calc(100% - 20px);
    padding: 10px 10px 0px 10px;
    height: 40vh;
    object-fit: cover;
    object-position: 50% 30%;
}

.photo-caption {
    width: 98%;
    font-size: 18px;
    color: var(--photo-caption-text);
    position: relative;
    z-index: 1;
}

.appear {
    animation: appear 2.5s infinite ease-in;
}

.disappear {
    animation: disappear 5s infinite ease-out;
}

#visionDiv {
    width: 95vw;
    margin-left: 2.5vw;
    background-color: var(--home-background);
    color: white;
    position: relative;
    z-index: 1;
}

#homeTitle {
    margin: 30px 0px 10px 0px;
    padding-top: 5px;
}

#visionStatement {
    padding-bottom: 5px;
}

